<template>
  <div class="container">
    <van-nav-bar
      left-arrow
      title=""
      @click-left="$goBack" />
    <div class="share-top">
      <div class="address">
        {{address}}
      </div>
      <div class="qrcode-box">
        <img :src="imgQrcode" alt="">
      </div>
      <div class="flex link-text">
        <div class="label"></div>
        <div class="cont flex-item">{{link}}</div>
        <img src="~@/assets/images/copy@2x.png" class="copy-icon clip-button" :data-clipboard-text="link" />
      </div>
    </div>
    <div class="share-num flex-middle">
      <div class="flex-item">{{reward}}</div>
      <div class="btn" v-if="Number(reward)" @click="takeReward"></div>
    </div>
    <div class="share-list">
      <div class="title"></div>
      <div class="row-th flex">
        <div class="text"></div>
        <div class="text">(wind token)</div>
        <div class="flex-item"></div>
      </div>
      <div class="row-td flex">
        <div class="text">oxd***4df</div>
        <div class="text">1000.00</div>
        <div class="flex-item">2021-02-15 12:00:00</div>
      </div>
    </div>
  </div>
</template>
<script>
// import VueQr from 'vue-qr'
import { Toast } from 'vant';
import QRCode from 'qrcode'
import Clipboard from 'clipboard'
import { reactive, toRefs, computed, watch} from "vue";
import BSNAbi from "@/utils/BSNAbi";
import tokenAbi from "@/utils/abi";
import config from "@/utils/config";
import BSNDataAbi from "@/utils/BSNDataAbi";
import {useStore} from "vuex";
export default {
  components: {
    // VueQr
  },
  setup () {
    const store = useStore()
    const data = reactive({
      records: [],
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      freezeContract: null,
      windTokenContract: null,
      imgQrcode: '',
      reward: '',
      link: '',
      initContract: () => {
        data.BsnContract = new data.web3.eth.Contract(BSNAbi, config.BsnAddress)
        data.BsnDataContract = new data.web3.eth.Contract(BSNDataAbi, config.BsnDataAddress)
        data.windTokenContract = new data.web3.eth.Contract(tokenAbi, config.windToken)
        data.initData()
        data.getReward()
      },
      takeReward: async () => {
        await data.BsnContract.methods.getReward().send({from: data.address})
        data.getReward()
      },
      getReward: async () => {
        let userInfo = await data.BsnDataContract.methods.getUserInfo(data.address).call();
        let windeciams = await data.windTokenContract.methods.decimals().call();
        data.reward = Number(userInfo.reward/Math.pow(10, windeciams)).toFixed(2);
      },
      initData: () => {
        data.link = `${window.location.origin}/#/?intro=${data.address}`
        QRCode.toDataURL(data.link, {margin: 0.5, width: 168})
      .then(url => {
        data.imgQrcode = url
      })
      .catch(err => {
        console.error(err)
      })
      var clipboard = new Clipboard('.clip-button')
      clipboard.on('success', function (e) {
        Toast('')
        e.clearSelection()
      })
      clipboard.on('error', function () {
        Toast('')
      }) 
      }
    })
     if (data.address) {
      data.initContract()
    }
     watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.container {
  color: #fff;
}
.share-top {
  background: rgba(26, 28, 37, .7);
  padding: 15px;
  margin: 15px;
  .address {
    background: #0D0E11;
    border-radius: 17px;
    padding: 8px 12px;
    color: rgba(251, 197, 9, 1);
    font-size: 13px;
  }
  .qrcode-box {
    width: 184px;
    height: 184px;
    margin: 22px auto;
    background: rgba(251, 197, 9, 0.12);
    padding: 8px;
  }
  .link-text {
    font-size: 15px;
    .label {
      margin-right: 15px;
    }
    .cont {
      text-align: right;
      word-break: break-all;
      color: rgba(251, 197, 9, 1);
    }
    .copy-icon {
      width: 13px;
      height: 13px;
      margin: 5px 0 0 14px
    }
  }
}
.share-num {
  margin: 15px;
  background: rgba(26, 28, 37, .7);
  border-radius: 25px;
  padding: 10px 15px;
  font-size: 15px;
  .btn {
    background: rgba(251, 197, 9, 1);
    height: 34px;
    line-height: 34px;
    border-radius: 34px;
    padding:  0 20px;
    color: rgba(13, 14, 17, 1);
    font-size: 14px;
  }
}
.share-list {
  margin: 15px 15px 0;
  border-radius: 25px 25px 0 0;
  background: rgba(26, 28, 37, 0.7);
  text-align: center;
  font-size: 12px;
  padding: 0 0 30px;
  .title {
    line-height: 50px;
    font-size: 17px;
  }
  .row-th {
    background: rgba(255, 255, 255, 0.04);
    color: rgba(255, 255, 255, 0.5);
    line-height: 40px;
  }
  .row-td {
    line-height: 33px;
  }
  .row-th,
  .row-td {
    padding: 0 14px;
    &>div:first-child {
      text-align: left;
    }
    &>div:last-child {
      text-align: right;
    }
    &>div:nth-child(2){
      color: rgba(251, 197, 9, 1);
    }
    .text {
      width: 100px;
    }
  }
}
</style>
